.popup-form {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    width: 350px;
    padding: 68px 64px;
    margin: 40px auto;
    background-color: $inverse-color;
}

.popup-form__input {
    flex-basis: 100%;
    padding-bottom: 12px;
    margin-bottom: 46px;
    font-family: $user-font;
    font-size: 14px;
    font-weight: 500;
    text-transform: uppercase;
    background-color: transparent;
    border: none;
    border-bottom: 2px solid $light-color;
    transition: border 0.2s;
    &::placeholder {
        color: $text-light;
    }
    &:focus {
        border-color: currentColor;
        &::placeholder {
            position: absolute;
            width: 1px;
            height: 1px;
            overflow: hidden;
            clip: rect(0 0 0 0);
        }
    }
}

.popup-form__send {
    margin-left: auto;
    margin-right: auto;
}