.site-footer__content {
  display: flex;
  justify-content: space-between;
  max-width: 760px;
  padding-top: 36px;
  padding-bottom: 48px;
}
@media (max-width: 768px) {
  .site-footer__content {
    flex-wrap: wrap;
    justify-content: center;
  }
}

.site-footer__info {
  flex-basis: 274px;
  display: flex;
  flex-direction: column;
}
@media (max-width: 768px) {
  .site-footer__info {
    order: 1;
    flex-basis: 286px;
  }
}

.site-footer__social {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 768px) {
  .site-footer__social {
    margin-bottom: 20px;
  }
}

.site-footer__social-item {
  flex-shrink: 0;
  width: 34px;
  height: 34px;
}

.site-footer__social-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: #373737;
  border-radius: 50%;
  transition: background-color 0.2s;
}
.site-footer__social-link:hover, .site-footer__social-link:focus {
  background-color: #B88B58;
}

.site-footer__description {
  max-width: 370px;
  font-size: 12px;
  font-weight: 500;
  line-height: 21px;
  color: #7e7e7e;
}
@media (max-width: 768px) {
  .site-footer__description {
    flex-basis: 100%;
    max-width: 100%;
    text-align: center;
    margin-bottom: 20px;
  }
}

.site-footer__copyright {
  margin-top: auto;
  font-weight: 500;
  text-transform: uppercase;
  color: #FFFFFF;
}
@media (max-width: 768px) {
  .site-footer__copyright {
    align-self: center;
  }
}

.site-header {
  position: relative;
  height: 100vh;
  min-height: 600px;
}

.site-header__topline {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
}

.site-header__slider {
  height: 100%;
}
.site-header__slider .slick-list {
  height: 100%;
}
.site-header__slider .slick-track {
  height: 100%;
}

.slick-dots {
  position: absolute;
  bottom: 8%;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: space-between;
  max-width: 64px;
  width: 100%;
}
.slick-dots li.slick-active > button {
  background-color: #B88B58;
}
.slick-dots button {
  padding: 5px;
  font-size: 0;
  background-color: transparent;
  border: 2px solid #B88B58;
  border-radius: 50%;
  cursor: pointer;
}
@media (orientation: portrait) {
  .slick-dots {
    bottom: 15%;
  }
}

.site-header__slide {
  position: relative;
  height: 100%;
  background-size: cover;
  background-position: center 5px;
}

.site-header__content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -35%);
  text-transform: uppercase;
  text-align: center;
  color: #FFFFFF;
}

.site-header__title {
  margin-bottom: 26px;
  font-size: 72px;
  line-height: 0.9em;
}
.site-header__title span {
  color: #B88B58;
}
@media (max-width: 992px) {
  .site-header__title {
    font-size: 60px;
  }
}
@media (max-width: 768px) {
  .site-header__title {
    font-size: 48px;
  }
}
@media (max-width: 572px) {
  .site-header__title {
    font-size: 42px;
  }
}
@media (max-width: 480px) {
  .site-header__title {
    font-size: 34px;
  }
}
@media (max-width: 360px) {
  .site-header__title {
    font-size: 32px;
  }
}

.site-header__description {
  margin-bottom: 50px;
  font-size: 24px;
  font-weight: 600;
}
.site-header__description a {
  color: inherit;
  transition: all 0.2s;
}
.site-header__description a:hover, .site-header__description a:focus {
  color: #B88B58;
}
@media (max-width: 480px) {
  .site-header__description {
    font-size: 22px;
  }
}
@media (max-width: 360px) {
  .site-header__description {
    font-size: 20px;
  }
}

.site-main {
  position: relative;
}

.site-main__btn-up {
  position: absolute;
  left: -60px;
  width: 64px;
  height: 64px;
}
@media (max-width: 1320px) {
  .site-main__btn-up {
    left: 20px;
  }
}

.site-section:nth-child(odd) {
  background-color: #EDEDED;
}
.site-section:nth-child(even) {
  background-color: #F7F7F7;
}